<template>
   <v-row justify="center">
    <v-dialog v-model="dialog"  persistent  max-width="600px">
      <v-card>

        <v-card-title>
          <span class="headline"> <v-icon color="primary">mdi-ballhorn</v-icon> Duyuru Ekle</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
                <v-col cols="12" sm="12">
      <v-text-field
        :error-messages="errTitle"
        v-model="annoucment.title"
        :label="$t('annoucment.title')"
        filled
        counter="50"
        hint="Duyuru Bağlığını Giriniz"
        background-color="transparent"
        ></v-text-field>
    </v-col>
    <v-col cols="12" sm="12">
 <v-textarea
 counter="140"
 :error-messages="errContent"
 v-model="annoucment.content"
          name="input-7-1 w-100"
          :label="$t('annoucment.content')"
          hint="Etkinlik Açıklamasını giriniz"
        ></v-textarea>
    </v-col>
    <v-col cols="12">
         <v-checkbox v-model="annoucment.isParent"
 :error-messages="errCheck"
          :label="`Duyuruyu Velilere gönder`"></v-checkbox>
    <v-checkbox v-model="annoucment.isTeacher"
     :error-messages="errCheck"
    :label="`Duyuruyu Öğretmenlere gönder`"></v-checkbox>
      <v-checkbox v-model="annoucment.isVote"
     :error-messages="errCheck"
    :label="`Duyuruyu Etkileşime Aç`"></v-checkbox>
    <small class="text-danger">Duyuruyu Etkileşime açtığınız takdirde Veliler veya Öğretmenler gönderilen bildirime katılım fikirlerini alabilirsiniz.</small>

    </v-col>
            </v-row>
    <div style="padding:20px 20px 0 20px" v-if="alert">

        </div>
          </v-container>
        </v-card-text>
         <v-card-actions>

          <v-spacer></v-spacer>
           <v-btn  class="ma-2" @click="closeModal()" color="info" dark>{{$t('generate.close')}} </v-btn>
           <v-btn :loading="loading" :disabled="loading" @click="sendAnnoucment()" class="ma-2" color="primary" dark>{{$t('generate.submit')}}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <snack-bar :snackbar="snackbar" @finished="snackbar=$event"></snack-bar>
  </v-row>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
props:{
      dialog:{
        default:false,
        type : Boolean,
        required:true
    },
},
computed:{
  ...mapGetters(['getActiveUser'])
},
methods:{
    closeModal(){
        this.dialog=false
        this.$emit('dialog',false)
        this.annoucment={
            title:"",
            content:"",
            isVote:false,
            isTeacher:false,
            isParent:false,
            sender:5,
            isPrivate:0
        }
    },
    sendAnnoucment(){
      this.errTitle=[]
      this.errContent=[]
      this.errCheck=[]
        this.loading=true
      let vm =  this.annoucment
      if(vm.title=='' ||vm.title.length>50 ||vm.content=='' || vm.content.length>140 ||(!vm.isTeacher && !vm.isParent)){
        if(vm.title==''){
          this.errTitle.push('Duyuru Başlığını girmek zorundasınız.')
        }
        if(vm.title.length>50){
          this.errTitle.push('Duyuru Başlığını en fazla 50 karakter olmalıdır.')
        }
        if(vm.content==''){
          this.errContent.push('Duyuru İçeriği girmek zorundasınız.')
        }
        if(vm.content.length>140){
          this.errTitle.push('Duyuru İçeriği en fazla 140 karakter olmalıdır.')
        }
        if(!vm.isTeacher && !vm.isParent){
          this.errCheck.push('Duyuru göndermek istediğiniz grupları seçiniz.')
        }
       this.loading=false
      }
      else{
          this.annoucment.isTeacher?this.annoucment.isTeacher=1:this.annoucment.isTeacher=0
          this.annoucment.isParent?this.annoucment.isParent=1:this.annoucment.isParent=0
          this.annoucment.isVote?this.annoucment.isVote=1:this.annoucment.isVote=0
          this.annoucment.sender = this.getActiveUser.id
        this.$api.post('announcement',this.annoucment).then(res=>{
           if(res.id){
             if(this.annoucment.isTeacher==1){
             this.$api.tnotify(this.annoucment.title,this.annoucment.content,'teachers')
             }
             if(this.annoucment.isParent==1){
             this.$api.tnotify(this.annoucment.title,this.annoucment.content+`${this.annoucment.isVote==1?' Lütfen Katılım Fikrinizi Belirtiniz.':''}`,'parents')
             }
               this.loading=false
               this.snackbar=true
               this.$emit('newAnnoucment',{...this.annoucment,id:res.id})
               this.closeModal()
           }
        })
      }
    }
},
data(){
    return{
        dialogLoading:false,
        date:[],
        snackbar:false,
        annoucment:{
            title:"",
            content:"",
            isTeacher:false,
            isVote:false,
            isParent:false,
            sender:5,
            isPrivate:0
        },
           alert:false,
           loading:false,
           errTitle:[],
           errContent:[],
           errCheck:[]
    }
}
}
</script>

<style>
@media only screen and (max-width: 900px) {
  .v-dialog {
    margin : 0px !important
  }
}
.el-upload, .el-upload-dragger {
    width: 100% !important;
}
</style>