<template>
  <v-container fluid >
     <loading v-if="pageLoading"/>
    <div v-else>
       <v-row>
       <v-col class="text-right my-4" cols="12">
         <v-btn color="primary" @click="dialog=true">Yeni Duyuru Ekle</v-btn>
       </v-col>
         <v-col cols="12" sm="4" v-for="(item,index) in announcement" :key="index">
             <v-card
    class="mx-auto"
    :color="item.color"
    dark
  >

    <v-card-title>
      <v-icon
        large
        left
        class="anime"
      >
        mdi-bullhorn
      </v-icon>
      <span class="title font-weight-light">{{item.title}}</span>
       <v-row

          align="center"
          justify="end"
        >
      <v-tooltip class="mx-2" v-if="item.isParent==1" bottom>
      <template v-slot:activator="{ on, attrs }">
        <span  @click="setEditAnno(item,index)" >
          <v-icon

          color="white"
          dark
          v-bind="attrs"
          v-on="on"
        >mdi-file-document-edit</v-icon>
        </span>
      </template>
      <span>Düzenle</span>
    </v-tooltip>
     <v-tooltip class="mx-2 " v-if="item.isParent==1" bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="processData={id:item.id,index:index};dialog3=true;">
          <v-icon
        class="ml-2"
          color="white"
          dark
          v-bind="attrs"
          v-on="on"
        >mdi-close-box</v-icon>
        </span>
      </template>
      <span>Duyuruyu Kaldır</span>
    </v-tooltip>
        </v-row>
    </v-card-title>

    <v-card-text class=" font-weight-bold">
     {{item.content}}
    </v-card-text>

    <v-card-actions>
      <v-list-item class="grow">
        <v-list-item-avatar color="grey darken-3">
          <v-img
            class="elevation-6"
            :src="item.image==0?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':item.image"
          ></v-img>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>{{item.name}} {{item.lastName}}</v-list-item-title>
        </v-list-item-content>

        <v-row
          align="center"
          justify="end"
        >
            <v-tooltip  class="mx-2" v-if="item.isVote==1" bottom>
      <template v-slot:activator="{ on, attrs }">
          <span class="mx-3">{{item.count}} <v-icon
          color="white"
          dark
          v-bind="attrs"
          v-on="on"
        >mdi-hand-okay</v-icon></span>
      </template>
      <span>{{item.count}} kişi katılım gösteriyor.</span>
    </v-tooltip>

         <v-tooltip class="mx-2" v-if="item.isTeacher==1" bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-icon
          color="white"
          dark
          v-bind="attrs"
          v-on="on"
        >mdi-teach</v-icon>
      </template>
      <span>Öğretmenler İçin</span>
    </v-tooltip>
             <v-tooltip class="mx-2" v-if="item.isParent==1" bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-icon
          color="white"
          dark
          v-bind="attrs"
          v-on="on"
        >mdi-account</v-icon>
      </template>
      <span>Veliler İçin</span>
    </v-tooltip>
        </v-row>
      </v-list-item>
    </v-card-actions>
  </v-card>
         </v-col>
         <v-col cols="12" v-if="done" class="text-center">
             Şuan tüm Duyularınızı görüyorsunuz..
         </v-col>
          <v-col v-else cols="12" class="text-center">
             <v-btn v-if="announcement.length!=0" :loading="loading" :disabled="loading" @click="pages()" color="primary">Daha Fazla Duyuru Göster</v-btn>
         </v-col>
     </v-row>
     <insert-annoucment-dialog @newAnnoucment="announcement.unshift($event)" @dialog="dialog=$event" :dialog="dialog"></insert-annoucment-dialog>
     <edit-annoucment-dialog :index="index" @edit="announcement[$event.index]=$event" :annoucment="editAnno" @dialog="dialog2=$event;editAnno=[];index=null" :dialog="dialog2"></edit-annoucment-dialog>
      <confirm :dialog="dialog3" @cancel="dialog3=false" @confirm="deleteAnno" />
      <snack-bar :snackbar="snackbar"></snack-bar>
    </div>
  </v-container >
</template>

<script>
import insertAnnoucmentDialog from '../../components/dialogs/insertAnnocumentDialogs'
import editAnnoucmentDialog from '../../components/dialogs/editAnnoucmentDialog'
export default {
  components:{
    insertAnnoucmentDialog,
    editAnnoucmentDialog
  },
data(){
    return{
        announcement:[],
        count:9,
        dialog3:false,
        snackbar:false,
        done:false,
        dialog:false,
        dialog2:false,
        loading:false,
        editAnno:{},
        index:0,
        pageLoading:true,
        processData:null
    }
},
methods:{

  deleteAnno(){
    this.$api.put('announcement',this.processData.id, {isActive:0}).then(result=>{
      if(result.result==1){
        this.snackbar=true
        this.announcement.splice(this.processData.index,1)
        this.dialog3 = false
            this.processData = null
         setTimeout(() => {
           this.snackbar=false
         }, 4000);
      }
      else{
            this.processData = null

      }
    })

  },
  setEditAnno(row,index){

    this.editAnno = {id:row.id, content:row.content, isTeacher:row.isTeacher, isParent: row.isParent,title:row.title}
    this.index = index
    this.dialog2=true

  },
 getRandomColor() {
  var letters = '0123456789ABCDEF';
  var color = '#';
  for (var i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
},
pages(){
    this.loading=true
    this.count += 9
      this.$api.get('h/announcementHelper?filter=isTeacher:1||isParent:1&sort=id&order=desc&limit=9&offset='+(9+this.count)).then(result=>{
        if(result.count!=0){
            result.body.forEach(res=>{
                res.color = this.getRandomColor()
                this.announcement.push(res)
                  this.done = false
                  this.loading=false
            })
        }
        else{
            this.done = true
             this.loading=false
        }
    })
}
},
mounted(){
    this.$api.get('h/announcementHelper?filter=isTeacher>=0&sort=id&order=desc').then(result=>{
        if(result.count!=0){
          if(result.status=="error"){
          console.log('girdik');

            this.announcement = []
            this.pageLoading = false

        }
        else{
          result.body.forEach(res=>{
              res.image = res.image.replace('public/',this.$api.BASE_API_URL)
                res.color = this.getRandomColor()
            })
            this.announcement = result.body
            this.pageLoading = false
        }
        }
        else{
          console.log('girdik');

            this.announcement = []
            this.pageLoading = false

        }
    })
}
}
</script>

<style scoped>
.anime{
    animation: rotate .5s linear infinite alternate-reverse;
}
@keyframes rotate {
    from{
        transform: rotate(20deg);
    }
    to{
        transform: rotate(-20deg);
    }
}

</style>